import React from 'react';

import ideaIcon from '../assets/images/idea.png';
import conversationIcon from '../assets/images/conversation.png';
import englishIcon from '../assets/images/english.png';


class Feature extends React.Component {
  render() {

  	return (
      <div>
         <section className="feature" id="feature">
            <div className="container">
               <div className="row">
                  <div className="col-md-12">
                     <div className="section-title">
                        <h2>USLUGE</h2>
                     </div>
                     <div className="feature-image feature-image--white-bg">
                        <div className="row">
                           <div className="col-md-6">
                              <img src={conversationIcon} alt="conversation"/>
                           </div>
                           <div className="col-md-6">
                              <div className="text-left">
                                 <h3>PRIJEVODI</h3>
                                 <p><em>stvorite si nove prilike</em></p>
                                 <p>elite ostaviti dobar dojam i izgraditi ugled u inozemstvu, a jezici vam nisu jaa strana? Priutite svojim itateljima jednostavan i jasan jezik. Uz nau pomo armirajte svojim tekstovima.</p>
                                 <p className="no-margin">Javite nam se ako trebate:</p>
                                 <ul>
                                    <li>ovjerene prijevode</li>
                                    <li>prijevode bez ovjere</li>
                                    <li>lekturu</li>
                                 </ul>
                              </div>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
         <section className="theme-bg">
            <div className="container">
                  <div className="feature-image">
                     <div className="row">
                        <div className="col-md-6">
                           <div className="text-left">
                              <h3 className="text-white">PISANJE SADRAJA</h3>
                              <p><em>privucite poglede klijenata</em></p>
                              <p>Znate koliko va proizvod vrijedi, ali to nikako ne znate pretoiti u rijei? Zapoeli ste s pisanjem, ali vas je snala kreativna blokada? Odahnite jer mi emo vam u tome pomoi. Kvalitetno i povoljno privlaimo poglede klijenata, vaa jedina zadaa je obratiti nam se.</p>
                           </div>
                        </div>
                        <div className="col-md-6">
                           <img src={ideaIcon} alt="idea"/>
                        </div>
                     </div>
                  </div>
            </div>
         </section>
         <section>
            <div className="container">
               <div className="feature-image">
                  <div className="feature-image feature-image--white-bg">
                     <div className="row">
                        <div className="col-md-6">
                           <img src={englishIcon} alt="english"/>
                        </div>
                        <div className="col-md-6">
                           <div className="text-left">
                              <h3>PODUKE</h3>
                              <p><em>jer dobra komunikacija je pola posla</em></p>
                              <p>Stiu inozemni klijenti, a vama je maca popapala jezik? Dosta je izbjegavanja poslovnih razgovora, sljedei ih sastanak izujte iz cipela. Mi smo tu da vam olakamo komunikaciju, a vi samo odaberite jezik.</p>
                              <p className="no-margin">Nudimo vam:</p>
                                 <ul>
                                    <li>teaj stranog jezika za poslovne potrebe</li>
                                    <li>opi teaj stranog jezika</li>
                                    <li>individualni teaj stranog jezika</li>
                                    <li>konverzacijski teaj</li>
                                    <li>online teaj stranog jezika</li>
                                 </ul>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
      </div>
  	);
  }
}


export default Feature;


